import React from 'react';
import Medal from './Medal';
import {
    Card,
    CardContent,
    Divider,
    Grid,
    Typography,
    Badge,
    IconButton,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';


const Country = (props) => {
    const { country, medals, onIncrement, onDecrement, onDelete, canDelete, canPatch } = props;

    const getMedalsTotal = (country, medals) => {
        let sum = 0;
        medals.forEach(medal => { sum += country[medal.name]; });
        return sum;
    }

    return (
        <Card
            className='country'
            sx={{
                minWidth: 275,
                bgcolor: "#404040",
                color: "white",
                boxShadow: " 10px 10px 20px 5px black"
            }}
        >
            <CardContent>
                <Grid container>
                    <Grid item xs={10}>
                        <Typography
                            className="name"
                            gutterBottom
                            variant="h5"
                            component="div"
                            fontFamily="Serif"
                            fontWeight="Bold"
                        >
                            <Badge color="secondary" badgeContent={getMedalsTotal(country, medals)}>
                                {country.name}
                            </Badge>
                        </Typography>
                    </Grid>
                    <Grid item xs={2}>
                        {canDelete && <IconButton
                            variant="text"
                            sx={{ color: "white" }}
                            onClick={() => onDelete(country.id)}
                        >
                            <DeleteIcon />
                        </IconButton>}
                    </Grid>
                </Grid>

                <Divider sx={{ bgcolor: "white" }} />

                {medals.map(medal =>
                    <Medal
                        key={medal.id}
                        country={country}
                        medal={medal}
                        canPatch={canPatch}
                        onIncrement={onIncrement}
                        onDecrement={onDecrement}
                    />
                )}
            </CardContent>
        </Card>
    );
}


export default Country;