import React from 'react';
import {
    Avatar,
    IconButton,
    Grid,
    Typography
} from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';

const Medal = (props) => {
    const { medal, country, onIncrement, onDecrement, canPatch } = props;
    return (
        <Grid container className="medals" mt={1.5}>

            <Grid item xs={6}>
                <Typography
                    variant="subtitle1"
                    component="div"
                    sx={{
                        mt: .7,
                        fontWeight: "bold",
                        fontFamily: "Serif"
                    }}
                >
                    {medal.name} medals:
                </Typography>
            </Grid>
            <Grid item xs={6}>
                <Grid container alignItems="right">

                    {canPatch && <IconButton
                        variant="text"
                        sx={{ color: medal.color }}
                        disabled={country[medal.name] === 0}
                        onClick={() => onDecrement(country.id, medal.name)}
                    >
                        <RemoveCircleIcon />
                    </IconButton>}

                    <Avatar sx={{ bgcolor: medal.color, color: "black", mr: -1, ml: -1 }}>
                        {country[medal.name]}
                    </Avatar>

                    {canPatch && <IconButton
                        variant="text"
                        sx={{ color: medal.color }}
                        onClick={() => onIncrement(country.id, medal.name)}
                    >
                        <AddCircleIcon />
                    </IconButton>}

                </Grid>
            </Grid>
        </Grid>
    );
}

export default Medal; 